@use '../util/' as *;

.toggle {
  display: grid;
  grid-template-columns: 1fr rem(60) 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: rem(4.8) rem(8);
  border: none;
  margin: 0;

  label {
    font-size: rem(14);
    font-weight: 700;
    color: var(--toggle);
    align-self: center;

    &[for='dark'] {
      grid-column: 1 / 2;
      justify-self: end;
    }
  }

  input[type='radio'] {
    margin: 0 rem(-2) 0 rem(-2);
    opacity: 0;
    width: rem(20);
    height: rem(24);

    &:focus ~ .toggle__button {
      border: 2px solid white;
    }
  }

  &__wrapper {
    position: relative;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    height: rem(24);
  }

  &__background {
    display: block;
    position: absolute;
    top: 0;
    border-radius: rem(12);
    background: linear-gradient(225deg, var(--toggle-bg-start) 0%, var(--toggle-bg-end) 98.02%);
    height: 100%;
    width: 100%;
  }

  &__button {
    position: absolute;
    left: rem(3);
    top: rem(3);
    right: 100%;
    background-color: var(--dark-bg);
    width: rem(18);
    height: rem(18);
    border-radius: 50%;
    transition: all 150ms ease-in-out;
  }
}
